import './App.css';
import Title from './title'
import Search from './search'
import ListFunc from './list'
import AddList from './add'
import { useState } from 'react'
import React from 'react'

let _ = require('lodash');

function App() {
  let [listelem,setLstelem] = useState({active:[],done:[]});
  let [visib,setVisib] = useState({
    active:'block',
    done:'block',
  });
  function tabChange(newValue){
    if(!newValue){
      setVisib({
        active:'block',
        done:'block'
      })
    }
    else if(newValue === 1){
      setVisib({
        active:'block',
        done:'none'
      })
    }
    else if(newValue === 2){
      setVisib({
        active:'none',
        done:'block'
      })
    }
  }
  function relocateElem(index,array){
    let protoObj = _.cloneDeep(listelem)
    if(!array){
      protoObj.done.push(protoObj.active.splice(index,1))
    }
    else if(array){
      protoObj.active.push(protoObj.done.splice(index,1))
    }
    setLstelem(protoObj)
  }
  function deleteActiveItem(index){
    let protoList = [].concat(listelem.active)
    protoList.splice(index,1)
    setLstelem({active:[...protoList],done:[...listelem.done]})
  }
  function deleteDoneItem(index){
    let protoList = [].concat(listelem.done)
    protoList.splice(index,1)
    setLstelem({active:[...listelem.active],done:[...protoList]})
  }
  function action(newElem) {
    setLstelem({active:[...listelem.active, newElem],done:[...listelem.done]})
  }
  return (
    <div className='main-block'>
      <Title active = {listelem.active.length} done = {listelem.done.length}/>
      <Search tabChange={tabChange}/>
      <ListFunc obj={listelem} deleteActiveItem={deleteActiveItem} deleteDoneItem={deleteDoneItem}
       visib={visib} relocateElem={relocateElem}/>
      <AddList act={action}/>
    </div>
  );
}

export default App;
