import './App.css'
import React from 'react'
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import Paper from '@material-ui/core/Paper';
import { Switch } from '@material-ui/core';
import styled from 'styled-components'

const ActiveEl = styled.div`
    display:${props => props.valueDisp.active};
`;
const DoneEl = styled.div`
    display:${props => props.valueDisp.done};
`;

function ListFunc(props){
    const ActiveLists = props.obj.active.map((item,index,array) => { 
        return(
            <Paper className='item-of-list' key={index.toString()}>
                <span>{item}</span>
                <div className='button-to-do'>
                    <Switch color="default" checked={false} onChange={(event) => props.relocateElem(index)} />
                    <IconButton aria-label="delete" onClick = {() => props.deleteActiveItem(index)}>
                        <DeleteIcon fontSize="small" />
                    </IconButton>
                </div>
            </Paper>
        )
    })
    const DoneLists = props.obj.done.map((item,index,array) => { 
        return(
            <Paper className='item-of-list' key={index.toString()}>
                <span>{item}</span>
                <div className='button-to-do'>
                    <Switch color="default" checked={true} onChange={() => props.relocateElem(index,true)}/>
                    <IconButton aria-label="delete" onClick = {() => props.deleteDoneItem(index)}>
                        <DeleteIcon fontSize="small" />
                    </IconButton>
                </div>
            </Paper>
        )
    })
    return (
        <div className='content-block'>
            <ActiveEl valueDisp={props.visib}>{ActiveLists}</ActiveEl>
            <DoneEl valueDisp={props.visib}>{DoneLists}</DoneEl>
        </div>
    )
}
export default ListFunc