import './App.css'
import './App.js'
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import { useState } from 'react'
import React from 'react';

function AddList({act}){
    let [current, setCurrent] = useState('')
    return (
        <div className='search-block add-block'>  
        <Paper elevation={0} className='backgroundPaper'>
            <TextField id="standard-textarea" max = {3} InputProps={{ inputProps: { min: 0, max: 10 } }}
                label="What need to be done" className='add-form' placeholder="What need to be done"
                onChange={(event) => setCurrent(event.target.value)} 
            />
            <Button variant="contained" size="large" 
                color="primary" 
                className='selector-item add-button' 
                onClick={(event) => {
                    event.preventDefault()
                    return act(current)}}>
                Add Todo Item
            </Button> 
        </Paper>
        </div>
        
    )
}

export default AddList